.logoPag {
  width: 60px;
}

* {
  box-sizing: border-box;
}

body {
  width: 100%;
  margin: 0px;
}

.mainPagInicio {
  width: 100%;
}
.mainPagInicio .imagenPagInicio {
  width: 100%;
}
.mainPagInicio .botonActivosPagInicio {
  z-index: 1;
  position: relative;
  color: gold;
}

.cabecera {
  background-color: rgb(27, 37, 128);
}
.cabecera .listaCabecera {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  gap: 25px;
  width: 100%;
  border-bottom: 2px solid rgb(196, 182, 182);
  padding: 10px;
  padding-bottom: 20px;
}

