.piePagina {
  background-color: rgb(27, 37, 128);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px;
}
.piePagina p {
  color: #FFFFFF;
  font-size: 12px;
  margin-left: 120px;
  font-family: Arial, Helvetica, sans-serif;
}

.imagenPie {
  margin-left: 120px;
}